@import '../../../shared/variables.scss';

@keyframes bounce-1 {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes bounce-2 {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0);
  }
}

.banner {
  @extend %fonts;
  background-color: $background;
  position: relative;

  &Wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      @include adaptive(width, 763, 263);
      @include adaptive(height, 869, 300);
      @include adaptive(top, 100, 400);
      @include adaptive(left, 1250, 100);
      background-image: url('./images/Wave.webp');
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
