.burger {
  position: relative;
  cursor: pointer;
  @include adaptive(width, 62, 30);
  @include adaptive(height, 37, 18);
  z-index: 1;

  &:hover {
    opacity: 0.7;
    transition: 0.5s;
  }

  &Line {
    @include adaptive(width, 61, 30);
    @include adaptive(height, 4, 2);
    background-color: $mainColor;
    border-radius: 3px;
    position: relative;

