.body {
  @extend %flex;
  justify-self: center;
  height: 100%;
  @include adaptive(margin-bottom, 350, 470);

  &Wrapper {
    box-sizing: border-box;
    @include adaptivePadding(padding, 98, 116, 34, 44);
    background: rgba(255, 255, 255, 0.02);
    backdrop-filter: blur(30px);
    position: relative;
    z-index: 5;
  }
}
.subtitle {
  color: $secondaryColor;
  @extend %fz-24-30;
  @include adaptive(margin-bottom, 67, 16);
  @include adaptive(width, 475, 172);
}

.title {
  position: relative;
  z-index: 1;
}

.imgWrapper {
