.imgBlock {
  @include adaptive(width, 926, 304);
  @include adaptive(height, 710, 108);
  position: absolute;
  right: 0;
  @include adaptive(top, 600, 800);
  @include adaptive(left, 35, -445);
  transform: translateY(-50%);
  object-fit: contain;
  z-index: 1;
}

.mobile {
  position: absolute;
  object-fit: contain;
  @include adaptive(width, 236, 79);
  @include adaptive(height, 449, 147);
  top: 0;
  @include adaptive(top, 0, 20);
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-name: bounce-1;
  animation-timing-function: ease;
  z-index: 10;

  &Block {
    position: absolute;
    @include adaptive(width, 236, 77);
    @include adaptive(height, 449, 147);
    @include adaptive(top, 0, 590);
    @include adaptive(left, 230, -400);
    z-index: 0;
  }

  &1Part {
    position: absolute;
    @include adaptive(top, 25, 5);
    @include adaptive(left, -50, -10);
    @include adaptive(width, 101, 33);
    @include adaptive(height, 94, 31);
    object-fit: contain;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-name: bounce-2;
    animation-timing-function: ease;
    z-index: 10;
  }

  &2Part {
    position: absolute;
    @include adaptive(top, 120, 40);
    @include adaptive(left, 170, 59);
    @include adaptive(width, 168, 55);
    @include adaptive(height, 127, 42);
    object-fit: contain;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-name: bounce-2;
    animation-timing-function: ease;
    z-index: 10;
  }

  &3Part {
    position: absolute;
    @include adaptive(top, 250, 80);
    @include adaptive(left, -50, -10);
    @include adaptive(width, 168, 55);
    @include adaptive(height, 127, 42);
    object-fit: contain;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-name: bounce-2;
    animation-timing-function: ease;
    z-index: 10;
  }

  &4Part {
    position: absolute;
    @include adaptive(top, 350, 127);
    @include adaptive(left, 140, 59);
    @include adaptive(width, 168, 45);
    @include adaptive(height, 127, 25);
    object-fit: contain;
    z-index: 1;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-name: bounce-2;
    animation-timing-function: ease;
    z-index: 10;
  }
}

@media screen and (max-width: 768px) {
  .header {
    @include adaptive(margin-bottom, 100, 58);
  }
  .body {
    display: flex;
    flex-direction: column;
    @include adaptive(margin-bottom, 431, 229);
  }

  .imgBlock {
    @include adaptive(top, 531, 289);
    @include adaptive(left, 330, 9);
  }
  .mobileBlock {
    @include adaptive(top, 500, -230);
    @include adaptive(left, 700, -30);
  }

  .mobile {
    @include adaptive(top, -550, 330);
    @include adaptive(left, -200, 100);

    &1Part {
      @include adaptive(top, -530, 335);
      @include adaptive(left, -250, 85);
    }

    &2Part {
      @include adaptive(top, -450, 365);
      @include adaptive(left, -50, 155);
    }

    &3Part {
      @include adaptive(top, -340, 410);
      @include adaptive(left, -250, 85);
    }

    &4Part {
      @include adaptive(top, -190, 450);
      @include adaptive(left, -50, 145);
    }
  }
}
